/* App.css */
body {
  font-family: "Poppins", Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-size: 12px;
}

.App {
  text-align: center;
  padding: 20px;
}

.search-container {
  text-align: center;
  margin-bottom: 20px;
}

input {
  font-family: "Poppins", Arial, sans-serif;
  padding: 8px;
  margin-right: 10px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 2000px;
}

.search-icon {
  margin-right: 8px;
  color: #aaa;
}


/* Styling for the menu bar */
.menu-bar {
  display: flex;
  align-items: center;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  padding: 8px;
}

.menu-icon {
  font-size: 24px;
  cursor: pointer;
  margin-right: 10px;
}

.menu-options {
  display: none;
}

.menu-bar.open .menu-options {
  display: block;
  position: absolute;
  background-color: #f2f2f2;
  z-index: 1;
  width: 350px;
  top: 40px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.menu-bar.open .menu-icon {
  margin-right: 0;
}

.menu-options label {
  display: block;
  margin: 8px;
}

/* highlight ... */
.highlight {
  background-color: yellow;
}

.patient-list {
  display: flex;
  justify-content: center;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ddd;
}

th, td {
  font-family: "Poppins", Arial, sans-serif;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}